@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

.main-container{
  display: grid;
  justify-items: center;
  align-items: center;
  min-height: 100vh;
  background-color: #9B5DE5;
}

.form-container, .info-container{
  display: grid;
  width: 300px;
  gap: 10px;
  padding: 20px;
  background-color: #00E0C2;
  border-radius: 10px;
}

input, textarea, select, .info-submitted{
  padding: 10px;
  font-size: .9rem;
  resize: vertical;
  border-radius: 5px;
  outline: none;
  border: none;
  background-color: white;
}


.btn{
  border: none;
  padding: 10px 0;
  border-radius: 30px;
  transition: .3s all;
  background-color: #eee;
}

.btn:hover{
  background-color: #eeeeeedd;
}

.text-comment{
  white-space: pre-line;
}